export default {
	data() {
		return {
			askItem: {},
			reply: '',
			goodsId: '',
			canReply: true,
			answerPlaceholder: '回答此问题,4~40个字',
		};
	},
	onLoad(options) {
		this.askItem = JSON.parse(decodeURIComponent(options.data));
		this.goodsId = this.askItem.goodsId;
		this.havePower();
	},

	methods: {

		// loadData(goodsId) {
		// 	var condition = {
		// 		"mallId": this.$base_api.MALL_ID,
		// 		"goodsId": goodsId
		// 	}
		// 	this.$minApi.uniapp_post(this.$base_api.selectAskAndReply, condition).then(res => {
		// 		if (res.success) {
		// 			this.askList = res.data;
		// 		} else {
		// 			this.$msg("获取问答列表信息失败{}", res.msg);
		// 		}
		// 	})
		// },

		inputAnswer(e) {
			this.reply = e.detail.value;
		},

		answer() {
			if (!this.global.isLogin(this)) {
				this.$openPage('Auth');
				return;
			}
			if (this.reply === null || this.reply.trim() === "" || this.reply.length < 4) {
				uni.showToast({
					icon: 'none',
					title: "回答至少四个字符",
					duration: 1000
				})
				return;
			}
			if (this.reply.length > 40) {
				uni.showToast({
					icon: 'none',
					title: "回答最多四十个字符",
					duration: 1000
				})
				return;
			}
			var condition = {
				"mallId": this.$base_api.MALL_ID,
				"goodsId": this.askItem.goodsId,
				"problem": this.problem,
				"reply": this.reply,
				"askId": this.askItem.aksId
			}
			this.$minApi.uniapp_post(this.$base_api.reply, condition).then(res => {
				if (res.success) {
					self = this;
					uni.showModal({
						title: '回答成功!',
						success: function(res) {
							if (res.confirm) {
								uni.navigateBack({
									goodsId: self.goodsId
								});
							} else {
								uni.navigateBack({
									goodsId: self.goodsId
								});
							}
						}
					});
				} else {
					this.$msg("提交失败，可能原因{}", res.msg);
				}
			})
		},

		// 判断自己是否有权限回答
		havePower() {
			var condition = {
				"mallId": this.$base_api.MALL_ID,
				"goodsId": this.askItem.goodsId,
				"askId": this.askItem.aksId
			}
			this.$minApi.uniapp_post(this.$base_api.canAnswer, condition).then(res => {
				console.log(res);
				if (res.success || res.success == true) {
					this.canReply = false;
				} else {
					this.canReply = true;
					this.answerPlaceholder = res.msg;
				}
			});
		}
	}

}
